<template>
  <div>
    <form method="post"
      novalidate>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('name')}">
          <input v-validate="'required|alpha_spaces|min:3'" name="name" id="name" class="mdl-textfield__input" v-model="clientParams.name">
          <label class="mdl-textfield__label" for="name">Name</label>
          <span class="mdl-textfield__error">{{ errors.first('name') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('gst_number')}">
          <input v-validate="'required|alpha_num|min:15'" name="gst_number" id="gst_number" class="mdl-textfield__input" v-model="clientParams.gst_number">
          <label class="mdl-textfield__label" for="gst_number">GSTIN</label>
          <span class="mdl-textfield__error">{{ errors.first('gst_number') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('address_line_1')}">
          <input v-validate="'required|min:3|max:254'" name="address_line_1" class="mdl-textfield__input"  v-model="clientParams.address_line_1">
          <label class="mdl-textfield__label" for="address_line_1">Address Line 1</label>
          <span class="mdl-textfield__error">{{ errors.first('address_line_1') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('address_line_2')}">
          <input v-validate="'min:3|max:254'" name="address_line_2" class="mdl-textfield__input" v-model="clientParams.address_line_2">
          <label class="mdl-textfield__label" for="address_line_2">Address Line 2</label>
          <span class="mdl-textfield__error">{{ errors.first('address_line_2') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('email')}">
          <input v-validate="'email'" name="email" class="mdl-textfield__input" v-model="clientParams.email">
          <label class="mdl-textfield__label" for="email">Email</label>
          <span class="mdl-textfield__error">{{ errors.first('email') }}</span>
        </div>
      </div>
      <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('contact_number')}">
        <input v-validate="'required|min:10|max:15'" name="contact_number" class="mdl-textfield__input" v-model="clientParams.contact_number">
        <label class="mdl-textfield__label" for="contact_number">Contact Number</label>
        <span class="mdl-textfield__error">{{ errors.first('contact_number') }}</span>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: 'client-form',
  props: {
    client: Object
  },
  data () {
    return {
      params: {
        name: '',
        gst_number: '',
        address_line_1: '',
        address_line_2: '',
        email: '',
        contact_number: ''
      }
    }
  },
  computed: {
    clientParams: {
      get () {
        return this.client || this.params
      }
    }
  },
  methods: {
    valid () {
      return this.$validator.validateAll()
    }
  }
}
</script>



